import React from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
const LaunchItem = ({
  launch: { flight_number, mission_name, launch_date_local, launch_success },
}) => {
  const missionSuccess = launch_success
    ? { color: '#28a745' }
    : { color: '#dc3545' };

  const formattedDate = moment(launch_date_local).format('YYYY-MM-DD HH:mm');

  return (
    <div className='card card-body mb-3'>
      <div className='row'>
        <div className='col-md-9'>
          <h4>
            Mission: <span style={missionSuccess}>{mission_name}</span>
          </h4>
          <p>Date: {formattedDate}</p>
        </div>
        <div className='col-md-3'>
          <Link to={`/launch/${flight_number}`} className='btn btn-secundary'>
            Launch Details
          </Link>
        </div>
      </div>
    </div>
  );
};

export default LaunchItem;
